import Vue from 'vue';
import i18n from '../../../i18n';
import XColumn from './deck.column.vue';
import XTl from './deck.tl.vue';
import XListTl from './deck.list-tl.vue';
import XHashtagTl from './deck.hashtag-tl.vue';
export default Vue.extend({
    i18n: i18n('deck/deck.tl-column.vue'),
    components: {
        XColumn,
        XTl,
        XListTl,
        XHashtagTl
    },
    props: {
        column: {
            type: Object,
            required: true
        },
        isStacked: {
            type: Boolean,
            required: true
        }
    },
    data() {
        return {
            edit: false,
            menu: [{
                    icon: 'cog',
                    text: this.$t('edit'),
                    action: () => {
                        this.edit = !this.edit;
                    }
                }]
        };
    },
    computed: {
        name() {
            if (this.column.name)
                return this.column.name;
            switch (this.column.type) {
                case 'home': return this.$t('@deck.home');
                case 'local': return this.$t('@deck.local');
                case 'hybrid': return this.$t('@deck.hybrid');
                case 'global': return this.$t('@deck.global');
                case 'list': return this.column.list.name;
                case 'hashtag': return this.$store.state.settings.tagTimelines.find(x => x.id == this.column.tagTlId).title;
            }
        }
    },
    methods: {
        onChangeSettings(v) {
            this.$store.commit('updateDeckColumn', this.column);
        },
        focus() {
            this.$refs.tl.focus();
        }
    }
});
